@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Long+Cang&display=swap);
.app {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.header {
  height: 20rem;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
  padding: 30px;
  background-color: #2e0a50;
}

.header h1 {
  color: #2e9244;
  font-size: 4.5rem;
  font-family: 'Long Cang', cursive;
  font-weight: bold;
}

.imgHeader {
  width: 20%;
}

.imgHeader img {
  width: 100%;
}

@media only screen and (max-width: 900px) {
  .header {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

@media only screen and (max-width: 600px) {
  .imgHeader {
    width: 60%;
  }

  .header h1 {
    font-size: 3rem;
  }
}
.main {
  flex: 1 1;
}

.container {
  width: 100%;
  height: 100%;
  padding: 40px;
  background-color: #8a2be2;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.content {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  box-sizing: border-box;
}

.card {
  margin: 10px;
  width: 570px;
  background-color:#e5e6f0;
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  border-radius: 10px;
  list-style: none;
  box-shadow: 0px 0px 5px #3c3344;
}
.image {
  width: 35%;
}

.card>.image>img {
  width: 100%;
  height: 100%;
  border-radius: 10px 0px 0px 10px;
}

.description {
  width: 65%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.name {
  color: #370f58;
  font-weight: bold;
  font-size: 1.5rem;
}

.boxInfo>p, .description>p {
  color: #59476d;
  font-size: 1rem;
  letter-spacing: .07rem;
  font-weight: bold;
  margin-right: 10px;
}

.boxInfo>h3, .description>h3 {
  color: #281a36;
  font-size: 1rem;
  letter-spacing: .05rem;
  font-weight: bold;
  margin-right: 10px;
}

.description>h3{
  margin-top: 10px;
}

.boxInfo {
  display: flex;
  align-items: flex-end;
}

@media only screen and (max-width: 600px) {
  .card {
    width: 100%;
    margin-left: 0px;
    margin-right:  0px;
    flex-direction: column;
  }
  .container {
     padding: 20px; 
  }
  .image {
    width: 100%;
  }
  .description {
    width: 100%;
    }

    .card>.image>img {
      border-radius: 10px 10px 0px 0px;
    }

    .description>p{
      margin-top: 0px;
    }
    
}

.footer {
  min-height: 9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: #2e0a50;
  
}

.footer h1 {
  color: aliceblue;
  font-size: 1.6rem;
}

@media only screen and (max-width: 600px) {
  .footer h1 {
    font-size: 2rem;
    
  }
  .footer {
    align-items: flex-end;
  }
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html, body, #root {
    max-width: 1440px;
    height: 100%;
}

body {
    background: #e5e6f0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body, input, button {
    font-family: Roboto, sans-serif;
}

